    position: static;
  }
}
@media (max-width: 767px) {
  .navigation--nojs {
    position: relative;
    margin-top: 96px;
  }
}

.navigation__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  position: relative;
}
@media (min-width: 768px) {
  .navigation__list {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    z-index: 0;
  }
}

@media (min-width: 768px) {
  .navigation__item:nth-child(3) {
    grid-column: 4/5;
  }
  .navigation__item:nth-child(7) {
    grid-column: 4/5;
  }
}

.navigation__link {
  font-family: inherit;
  color: #ffffff;
  font-size: 18px;
  font-weight: 700;
  line-height: 26px;
  text-transform: uppercase;
  display: block;
  padding: 16px 40px 12px;
  background-color: #81b3d2;
  text-decoration: none;
  text-align: center;
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
  border-top: 1px solid rgba(255, 255, 255, 0.2);
  cursor: pointer;
}
@media (min-width: 768px) {
  .navigation__link {
    height: 100%;
    padding: 18px 10px 17px;
    font-size: 14px;
    line-height: 21px;
    color: #000000;
    background-color: #ffffff;
    border: none;
  }
  .navigation__link--current {
    color: #766357;
  }
}
.navigation__link:hover {
  background-color: #669ec0;
}
@media (min-width: 768px) {
  .navigation__link:hover {
    color: #81b3d2;
    background-color: transparent;
  }
}
.navigation__link:active {
  color: #ffffff4d;
  background-color: #5496bd;
}
@media (min-width: 768px) {
  .navigation__link:active {
    color: #000000;
    opacity: 0.3;
    background-color: transparent;
  }
}

.navigation__toggle {
  position: absolute;
  right: 0;
  display: block;
  border: none;
  background-color: transparent;
  cursor: pointer;
  z-index: 2;
  width: 66px;
  height: 56px;
  padding: 0 20px;
}
@media (min-width: 768px) {
  .navigation__toggle {
    display: none;
  }
}

@media (max-width: 767px) {
  .navigation--closed .navigation__list {
    display: none;
  }

  .navigation--nojs .navigation__toggle {
    display: none;
  }

  .navigation--nojs .navigation__list {
    display: block;
  }

  .navigation--nojs .navigation {
    position: static;
  }
}
.navigation--closed .navigation__toggle-dash {
  position: absolute;
  display: block;
  width: 23px;
  height: 4px;
  background-color: #81b3d2;
}

.navigation--closed .navigation__toggle-dash::before {
  content: "";
  position: absolute;
  top: 9px;
  left: 0;
  width: 23px;
  height: 4px;
  background-color: #81b3d2;
}

.navigation--closed .navigation__toggle-dash::after {
  content: "";
  position: absolute;
  top: -9px;
  left: 0;
  width: 23px;
  height: 4px;
  background-color: #81b3d2;
}

.navigation--opened .navigation__list::after {
  content: "";
  position: absolute;
  bottom: 0;
  height: 7px;
  width: 100%;
  background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.2) 100%);
}

.navigation--opened .navigation__toggle-dash {
  position: absolute;
  display: block;
  width: 25px;
  height: 4px;
  background-color: #ffffff;
  transform: rotate(45deg);
}

.navigation--opened .navigation__toggle-dash::after {
  content: "";
  position: absolute;
  display: block;
  width: 25px;
  height: 4px;
  transform: rotate(90deg);
  background-color: #ffffff;
}

.navigation--opened .navigation__toggle:hover .navigation__toggle-dash {
  opacity: 0.6;
}

.navigation--opened .navigation__toggle:active .navigation__toggle-dash {
  opacity: 0.3;
}

.navigation--closed .navigation__toggle:hover .navigation__toggle-dash {
  background-color: rgba(129, 179, 210, 0.6);
}
.navigation--closed .navigation__toggle:hover .navigation__toggle-dash::before {
  background-color: rgba(129, 179, 210, 0.6);
}
.navigation--closed .navigation__toggle:hover .navigation__toggle-dash::after {
  background-color: rgba(129, 179, 210, 0.6);
}

.navigation--closed .navigation__toggle:active .navigation__toggle-dash {
  background-color: rgba(129, 179, 210, 0.3);
}
.navigation--closed .navigation__toggle:active .navigation__toggle-dash::before {
  background-color: rgba(129, 179, 210, 0.3);
}
.navigation--closed .navigation__toggle:active .navigation__toggle-dash::after {
  background-color: rgba(129, 179, 210, 0.3);
}

