.field {
  position: relative;
}

.field__label {
  display: block;
  padding-left: 16px;
  color: #333333;
  font-size: 14px;
  line-height: 21px;
  text-transform: uppercase;
}
.field__label--custom {
  display: flex;
  align-items: center;
  padding: 10px 10px 10px 61px;
  font-size: 18px;
  line-height: 26px;
  font-weight: 700;
  text-transform: none;
  cursor: pointer;
}

.field__input {
  width: 100%;
}
.field__input:focus {
  outline: 2px solid #000000;
}

.field__radio:disabled + .field__custom {
  opacity: 0.3;
  cursor: default;
}

.field__checkbox:disabled + .field__custom {
  opacity: 0.3;
  cursor: default;
}

.field__custom {
  display: block;
  position: absolute;
  left: 0;
  width: 46px;
  height: 46px;
  background-color: #f2f2f2;
  border-radius: 4px;
  cursor: pointer;
}
.field__custom--radio {
  border-radius: 50%;
}
.field__custom:hover {
  background-color: #e5e5e5e5;
}

.field__radio:checked + .field__custom--radio::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background-color: #000000;
}

.field__checkbox:checked + .field__custom--checkbox::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 22px;
  height: 16px;
  background-image: url("../img/decor-icons/tick.svg");
}

.field__radio:focus + .field__custom--radio {
  outline: 2px solid #000000;
}

.field__checkbox:focus + .field__custom--checkbox {
  outline: 2px solid #000000;
}

.field__label--custom:active .field__custom--radio::before {
  background-color: #3333334d;
}
.field__label--custom:active .field__custom {
  background-color: #d5d5d5;
}

.field__label--checkbox:active .field__custom--checkbox::before {
  opacity: 0.3;
}
.field__label--checkbox:active .field__custom {
  background-color: #d5d5d5;
}

.field-icon::after {
  content: "";
  position: absolute;
  right: 0;
  width: 58px;
  height: 58px;
  background-color: #e6e6e6;
  background-repeat: no-repeat;
  background-position: 50% 50%;
}
.field-icon--phone::after {
  background-image: url("../img/decor-icons/phone.svg");
}
.field-icon--mail::after {
  background-image: url("../img/decor-icons/mail.svg");
}

